.cat-info {
  max-width: 900px;
  gap: 15px;
  margin-top: 15px;
  display: flex;
}

.loader {
  width: 96px;
  height: 16px;
  box-sizing: border-box;
  background-color: #fff;
  background-image: linear-gradient(45deg, rgba(0, 0, 0, .25) 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, .25) 50%, rgba(0, 0, 0, .25) 75%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0));
  background-size: 1em 1em;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 30px;
  animation: barStripe 1s linear infinite;
  display: inline-block;
}

@keyframes barStripe {
  0% {
    background-position: 1em 0;
  }

  100% {
    background-position: 0 0;
  }
}

.error {
  color: red;
  font-size: 15px;
}

#breedSelect {
  width: 180px;
  height: 40px;
  border-radius: 10px;
  font-size: 15px;
}

